<template>
    <div class="post" v-for="posting in dataList" :key="posting">
        <div class="post-header">
            <div class="profile" :style="{ backgroundImage : `url(${posting.userImage})`}"></div> 
            <!-- :style="~" 집어넣을 때 object 자료도 가능 (주로 해당 방식 사용함) -->
            <!-- 예를들어, style css background-image -> backgroundImage로 표현 해서 사용해야함 snake-case를 camel-case형태로 적용해야 인식함 -->
            <!-- es6 신문법인 벡틱(`) 활용하여 태그와 데이터값 같이 사용하면 편함 -->
            <span class="profile-name">{{ posting.name }}</span>
        </div>
        <div class="post-body" :style="{ backgroundImage : `url(${posting.postImage})` }"></div>
        <div class="post-content">
            <p>{{posting.likes}} Likes</p>
            <p>
                <strong>{{posting.name}}</strong> 
                {{posting.content}}
            </p>
            <p class="date">{{posting.date}}</p>
        </div>
    </div> 
</template>

<script>
export default {
    name : 'comp-post',
    props : {
        dataList : Array
    }


}
</script>

<style>
.post {
  width: 100%;
}
.profile {
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
.post-body {
  height: 450px;
  background-position: center;
  background-size: cover;
}
.post-content {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.date {
  font-size: 11px;
  color: grey;
  margin-top: -8px;
} 
</style>