<template>
    <div class="header">
        <ul class="header-button-left">
          <li @click="step--">Cancel</li>
        </ul>
        <ul class="header-button-right">
          <li v-if="step == 1" @click="step++">Next</li>
          <li v-if="step == 2" @click="publish">발행</li>
        </ul>
        <img src="./assets/logo.png" class="logo" />
    </div>

    <!-- 하위 > 하위 컴포넌트에서 해당 배열아이템 값 사용하고싶으면, 두번 props 이용해서 전달해주면됨-->
    <Container :dataList="dataList" :step="step" :fileImage="fileImage" /> 
    <!-- 더보기 버튼을 누르면,
    1. 서버에서 추가 게시물을 가져옴 : axios.get() 이용!~
    2. 그걸 <post>로 보여줄 것 : 함수에 data.result 값을 push를 통해 dataList 배열에 넣어줌 ['','','',''] 
    -->
    <button v-if="step == 0" @click="more">더보기</button>

    <div class="footer">
        <ul class="footer-button-plus">
        <input @change="upload" type="file" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
        </ul>
    </div>
  
</template>

<script>
import Container from './components/Container.vue';
import postdata from './assets/postdata.js';
import axios from 'axios';

// axios 사용법 추가내용
// post 요청은 axios.post()
// get 요청은 axios.get();
// 실패시 실행할 코드는 .catch()

// axios.post('URL' , {name : 'kim'}).then().catch((err) => {
//   err
// })

export default {
    name : 'App',
    data() {
      return {
          dataList : postdata,
          count : 0, //count : 더보기 버튼 클릭시 전달할 api 경로값 체크하기 위한 용도
          step : 0,
          fileImage : ''
      }
    },
    components : {
      Container
    },
    methods : {
      more() {
        // axios로 ajax요청하는 법
        axios.get(`https://codingapple1.github.io/vue/more${this.count}.json`)
        .then(result => {
          // 요청 성공시 실행할 코드 출력
          this.dataList.push(result.data);
          this.count++;
          console.log(result.data);
        })
        .catch(err => {
          // 실패시 에러 출력
          console.log(err);
        })
      },
      upload (e) {
        let file = e.target.files; // 내가업로드한 파일 저장되어 있음.
        //console.log(file[0].type); //.type속성은 이미지 확장자 제한걸때 해당 속성값으로 업로드한 확장자 찾아서 분기처리하여 사용함
        let fileURL =  URL.createObjectURL(file[0]); //file[0] 데이터의 URL을 생성해줌.
        this.fileImage = fileURL; // fileURL로 생성된 이미지 url을 fileImage 변수에 저장, 이후 해당값 props로 넘겨서 자식 컴포넌트에서 사용하도록 해주어야함
        this.step = 1; // 또는 this.step++;
      },
      publish () {
        let myData = {
          name: "lee Hyun",
          userImage: "https://placeimg.com/100/100/arch",
          postImage: this.fileImage,
          likes: 53,
          date: "June 15",
          liked: false,
          content: "테스트",
          filter: "perpetua"
        }
        let testPub = this.dataList.unshift(myData) // unshift -> 왼쪽에 있는 array('dataList')에 ('myData')를 추가
        console.log(testPub)
        this.step = 0;
      }
    }

}
</script>


